<template>
  <v-navigation-drawer v-model="drawer" :mini-variant.sync="mini" permanent class="float-left" id="drawer-menu">
    <v-list-item class="px-3">
      <v-list-item-avatar style="padding: 3px">
        <v-img :src="logo"></v-img>
      </v-list-item-avatar>
      <v-list-item-title>Rubix Platform</v-list-item-title>
      <v-btn icon @click.stop="minimizeMenu">
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
    </v-list-item>

    <v-divider></v-divider>
    <v-list dense nav>
      <div v-for="item in items" :key="item.key" style="text-align: start">
        <!-- no child -->
        <v-list-item v-if="!item.children" link :to="item.link">
          <v-list-item-icon>
            <v-tooltip right>
              <template v-slot:activator="{ on, attrs }">
                <v-icon v-bind="attrs" v-on="on"> {{ item.icon }} </v-icon>
              </template>
              <span>{{ item.title }}</span>
            </v-tooltip>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- have childs -->
        <v-list-group
          class="parent-bar"
          v-if="item.children && item.children.length > 0"
          :key="item.key"
          no-action
          :prepend-icon="item.icon"
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item v-for="sub in item.children" :key="sub.title" link :to="sub.link" style="color: white">
            <v-list-item-icon class="sub-service-icon">
              <v-icon>{{ sub.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ sub.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </div>
      <v-list-item v-bind:key="iframe._id" v-for="iframe in iframes" link>
        <v-list-item-icon @click="onClickIframeItem(iframe)">
          <v-tooltip right>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on"> {{ `mdi-${iframe.icon}` }} </v-icon>
            </template>
            <span>{{ iframe.name }}</span>
          </v-tooltip>
        </v-list-item-icon>
        <v-list-item-content @click="onClickIframeItem(iframe)">
          <v-list-item-title style="text-align: start">{{ iframe.name }}</v-list-item-title>
        </v-list-item-content>
        <v-list-item-action v-if="iframe.type !== 'iframeOnly'">
          <v-icon @click="clickOnLinkIcon(iframe)" small color="grey lighten-1">mdi-open-in-new</v-icon>
        </v-list-item-action>
      </v-list-item>
      <v-list-item link :to="'/setting'">
        <v-list-item-icon>
          <v-tooltip right>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on"> mdi-settings </v-icon>
            </template>
            <span>Settings</span>
          </v-tooltip>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title style="text-align: start">Settings</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <template v-slot:append>
      <div v-if="deviceInfo" :style="mini ? 'display:none' : 'text-align: start; padding: 10px; font-size: 0.8125rem;'">
        <div><span  class="warning--text">Client Name:</span> <span>{{deviceInfo.client_name}}</span></div>
        <div><span class="warning--text">Site Name:</span> <span>{{deviceInfo.site_name}}</span></div>
        <div><span class="warning--text">Device Name:</span> <span>{{deviceInfo.device_name}}</span></div>
      </div>
      <v-row :style="mini ? 'display:block' : 'padding: 0 5px;'">
        <nav-bar-three-dot-settings :class="mini ? 'ml-3' : ''"></nav-bar-three-dot-settings>
        <div class="radio-btn">
          <input
            type="radio"
            class="radio-input primary--text"
            :checked="setting.lockSidebar"
            @click="() => changeLock()"
            :class="mini ? 'mb-5 ml-7' : ''"
          />
          <label style="margin-bottom: 5px" v-if="!mini"><v-icon> mdi-lock </v-icon></label>
        </div>
      </v-row>
    </template>
  </v-navigation-drawer>
</template>

<script>
import logo from '../../assets/nube-frog-green.png';
import { sidebarItems } from '../../constants/appConstant';
import UpdatePasswordForm from './UpdatePasswordForm';
import _cloneDeep from 'lodash/cloneDeep';
import NavBarThreeDotSettings from './NavBarThreeDotSettings';
import axios from '../../axios';

export default {
  components: { UpdatePasswordForm, NavBarThreeDotSettings },
  props: ['drawer'],
  data: () => ({
    mini: true,
    logo,
    sidebarItems,
    password: {},
    passData: '',
    dialog: false,
    css: '',
    test: false,
    deviceInfo: {},
  }),
  computed: {
    iframes() {
      const { iframes } = this.$store.state;
      return iframes.filter(i => i.show);
    },
    items() {
      const { setting, mqttRestBridgeConfig } = this.$store.state;
      const menuItems = _cloneDeep(sidebarItems);
      const result = [];
      for (const item of menuItems) {
        if (setting[item.key]) {
          result.push(item);
        }
        if (item.key == 'masterSlaves' && mqttRestBridgeConfig && mqttRestBridgeConfig.master) {
          result.push(item);
        }
      }
      return result;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  mounted() {
    this.mini = !this.setting.lockSidebar;
    this.getDeviceInfo();
  },
  updated() {
    this.css =
      !this.setting.lockSidebar && this.mini
        ? 'position:absolute;bottom:50px'
        : 'position:absolute;bottom:13px;right:10px';
  },
  methods: {
    onClickIframeItem({ ipType, port, type, ip, _id }) {
      let url = `http://${ip}:${port}`;
      if (ipType === 'userBrowserIp') {
        url = `http://${location.hostname}:${port}`;
      }
      if (type === 'linkOnly') {
        window.open(url, '_blank');
        return;
      }
      this.$router.replace(`/iframe/${_id}`);
    },
    clickOnLinkIcon({ ip, port, ipType }) {
      let url = 'http://' + ip + ':' + port;
      if(ipType === 'userBrowserIp'){
        url= `http://${location.hostname}:${port}`;
      }
      window.open(url, '_blank');
    },
    onCancel() {
      this.dialog = false;
      this.test = false;
    },
    changeLock() {
      this.setting.lockSidebar = !this.setting.lockSidebar;
      axios.put(`setting`, this.setting).then(result => {
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!');
        }
      });
    },
    minimizeMenu() {
      if (!this.setting.lockSidebar) {
        this.mini = !this.mini;
      }
    },
    getDeviceInfo() {
      axios.get(`rubix-service/plat`).then(data => {
        this.deviceInfo = data;
      });
    },
  },
};
</script>
<style >
.v-list-group__items > .v-list-item {
  padding-left: 8px !important;
}
.parent-bar > .v-list-group__items {
  background-color: #1e1e1e7d;
  margin: 0 -5px;
}
.v-application--is-ltr .v-list--dense.v-list--nav .v-list-group--no-action > .v-list-group__items > .v-list-item {
  padding-left: 60px !important;
}
.v-application--is-ltr .v-list-item__action:first-child,
.v-application--is-ltr .v-list-item__icon:first-child {
  margin-right: 24px;
}
.radio-btn {
  display: flex;
  margin: auto 0;
}
.radio-input {
  width: 24px;
  height: 21px;
  margin-top: 4px;
}
</style>
